<template>
  <section id="links" name="links">
    <h2>Telegram</h2>
    <section class="links-list">
      <a
        v-for="(link, index) in telegram"
        :key="index"
        :href="'tg://resolve?domain=' + link.chatname"
      >
        <div class="links-item" :class="link.type">
          <div class="links-cover">
            <img :src="require('@/assets/images/links/' + link.chatname + '.jpg')">
          </div>
          <div class="links-info">
            <div class="upper">
              <div class="link-title">
                <h3>{{ link.title }}</h3>
                <div class="reason" v-if="link.reason" :data-reason="link.reason">
                  <font-awesome-icon icon="exclamation-circle" class="reason-icon"/>
                </div>
              </div>
              <p v-if="link.description">{{ link.description }}</p>
            </div>
            <div class="bottom">
              @{{ link.chatname }}
            </div>
          </div>
        </div>
      </a>
    </section>
  </section>
</template>

<script>
import links from '@/data/links';

export default {
  data() {
    return {
      telegram: links.telegram,
      title: {
        en: 'Links',
        ru: 'Ссылки',
        by: 'Спасылкі',
      },
    };
  },
};
</script>

<style>

#links {
  max-width: 680px;
  margin: auto;
}

.links-list {
  text-align: left;
}

.links-list a:hover .links-item {
  background: #f1f1f1;
}

.links-list p {
  margin-top: 0;
}

.links-item {
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
  box-sizing: border-box;
}

.links-cover {
  height: 120px;
}

.links-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5px 10px;
  width: 100%;
}

.links-item.true {
  border-left: #60f560 solid 5px;
}
.links-item.alert {
  border-left: red solid 5px;
}
.links-item.warning {
  border-left: orange solid 5px;
}

.links-item.true .reason-icon {
  color: #60f560;
}
.links-item.alert .reason-icon {
  color: red;
}
.links-item.warning .reason-icon {
  color: orange;
}

.link-title {
  display: flex;
  align-items: center;
}

.links-info > div {
  position: relative;
}

.links-info .reason {
  margin-left: 5px;
}

.reason-icon {
  opacity: 0.5;
}
.links-info .reason:before {
  content: attr(data-reason);
  box-shadow: #a5a5a5 0px 0px 5px 1px;
  border: 1px solid white;
  width: 100%;
  position: absolute;
  visibility: hidden;
  background: white;
  padding: 10px;
  left: 0;
  top: 10px;
  z-index: 10;
  border-radius: 5px;
}
.links-info .reason:hover:before {
  visibility: visible;
}

.links-info .reason_window {
  position: absolute;
  padding: 5px;
  background: white;
  z-index: 10;
  top: 0;
  width: 100%;
  visibility: hidden;
}

.links-cover img{
  object-fit: cover;
  width: 120px;
  height: 120px;
}

#links h4, #links h3 {
  margin: 0;
}


</style>



