<template>
  <section id="events" name="events">
    <h2>{{ title[$i18n.locale] }}</h2>
    <router-link v-for="(event, path) in events" :to="'/' + path" v-bind:key="path">
      {{ event.title[$i18n.locale] }}
    </router-link>
  </section>
</template>

<script>
import events from '@/data/events';

export default {
  data() {
    return {
      events,
      title: {
        en: 'Events',
        ru: 'События',
        by: 'Падзеі',
      },
    };
  },
};
</script>

<style scoped>
a {
  margin-right: 5px;
}
a:visited {
  color: inherit;
}
</style>



