<template>
  <paragrapher
    :paragraphs='event.paragraphs'
    :title='event.title'
    :description='event.description'
    :hashtags='event.hashtags'
  ></paragrapher>
</template>

<script>

import Paragrapher from '@/components/Paragrapher';
import events from '@/data/events';

export default {
  name: 'Event',
  components: {
    paragrapher: Paragrapher,
  },
  data() {
    return {
      event: events[this.$route.path.replace('/', '')].file.default,
    };
  },
};
</script>



