<template>
<div class="events-container" id="events" ref="events">
  <div class="events-header">
    <h3 class="">{{ title[$i18n.locale] }}</h3>
    <p class="">{{ description[$i18n.locale] }}</p>
    <h4>{{ labels.hashtags[$i18n.locale] }} {{ hashtags }}</h4>
  </div>
  <div class="events-list">
    <div
      class="event"
      v-for="(paragraph, index) in paragraphs"
      :key="index"
      >
      <div class="">
        <h3 class="">{{ paragraph.date }}</h3>
        <p
          class=""
          v-html="paragraph.body[$i18n.locale]"
          v-linkified:options="{ className: 'event-link' }"
        />
      </div>
    </div>
  </div>
</div>
</template>


<script>
export default {
  props: ['paragraphs', 'title', 'description', 'hashtags'],
  data() {
    return {
      labels: {
        hashtags: {
          en: 'Recommended hashtags for posts:',
          ru: 'Рекомендуемые хэштеги для постов:',
          by: 'Рэкамендаваны хэштэг для пастоў:',
        },
      },
    };
  },
};
</script>

<style>
.events-container {
  text-align: left;
  max-width: 600px;
  margin: 0 auto;
  padding: 7px 1px;
  background: #e6ecf0;
}


.events-container h3 {
  color: black;
  margin: 3px auto;
  font-size: 14px;
}

.events-container h4 {
  margin: 6px auto;
  font-size: 12px;
}

.events-header h3 {
  font-size: 18px;
}

.events-container p {
  margin: 3px auto;
}

.events-header, .event {
  padding: 10px 5px;
  background: white;
  margin: 0 auto;
  border: solid 1px #e6ecf0;
}

</style>



